<template>

	<div class="searchwp-cro metabox-holder">

		<ul class="searchwp-cro-triggers-actions">
			<li class="searchwp-cro-trigger-query">
				<input
					type="text"
					v-model="newQuery"
					@keyup.enter="addTrigger(newQuery)"
				/>
			</li>
			<li class="searchwp-cro-add-trigger">
				<button
					class="button"
					@click="addTrigger(newQuery)"
				>Add Search Query</button>
			</li>
			<li class="searchwp-cro-save">
				<button
					class="button button-primary"
					@click="save()"
				>Save</button>
			</li>
		</ul>

		<div class="searchwp-cro-triggers postbox">

			<div class="inside">

				<p v-if="!triggers.length" class="description">Enter a search query to begin customizing results.</p>

				<div class="searchwp-cro-triggers-container">
					<Trigger
						v-for="(trigger, triggerIndex) in triggers"
						:key="'trigger' + triggerIndex"
						:query="trigger.query"
						:engine="trigger.engine"
						:exact="trigger.exact"
						:promoted="trigger.promoted"
						:expanded="trigger.expanded"
					/>
				</div>

			</div>

		</div>

	</div>

</template>

<script>
import Vue from 'vue';
import Trigger from './components/Trigger';

export default {
	name: 'CustomResultsOrder',
	components: {
		Trigger
	},
	methods: {
		addTrigger(newQuery) {
			if (newQuery.length < 1) {
				alert('Query cannot be empty!');
				return;
			}

			/**
			 * This was originally to prevent collisions but after realizing
			 * there are use cases for having the same search query show different
			 * results based on which engine(s) are used, it opens up the possibility
			 * for the same search query used for different engines, and if there is a
			 * collision we're just going to have a rule that the first applicable
			 * results set will be used. If problematic we can add UI feedback later.
			 */
			// if (this.triggersQueries.indexOf(newQuery) > -1) {
			// 	alert('Already in use!');
			// 	return;
			// }

			let trigger = new Vue.Factory('trigger');

			// Enforce our defaults.
			trigger.query = newQuery;
			trigger.engine = {
				name: 'default',
				label: 'Default'
			};
			trigger.expanded = true;

			// Add this trigger to the top of the list.
			this.triggers.unshift(trigger);

			// Reset UI.
			this.newQuery = '';
		}
	},
	created() {
		this.triggers = this.$root.$data.triggers;
	},
	computed: {
		// triggersQueries() {
		// 	return this.triggers.map(a => a.query);
		// }
	},
	data() {
		return {
			newQuery: '',
			triggers: []
		}
	}
}
</script>

