<template>

	<div class="searchwp-cro-trigger">

		<h3 class="searchwp-cro-trigger__heading">
			<span @click="toggleDetails()" class="searchwp-cro-trigger__label">
				<span v-if="!showingDetails" class="dashicons dashicons-arrow-right"></span>
				<span v-else class="dashicons dashicons-arrow-down"></span>
				<span class="searchwp-cro-trigger__label-details">
					{{ query }}
					<span>{{ triggerEngine.label }} Engine</span>
				</span>
			</span>
			<button class="searchwp-cro-trigger__remove" @click="remove()">Remove</button>
		</h3>

		<div v-if="showingDetails" class="searchwp-cro-trigger__details">

			<div class="searchwp-cro-trigger__details-settings">
				<p>Searches for <strong><em>{{ query }}</em></strong> are being customized for the following engine:</p>
				<div class="searchwp-cro-trigger__engine">
					<multiselect
						v-model="triggerEngine"
						placeholder="Choose engine(s)"
						label="label"
						track-by="name"
						:hideSelected="true"
						:options="engineOptions"
						:multiple="false"
					></multiselect>
				</div>
			</div>

			<h4>Search Results</h4>

			<div :class="['searchwp-cro-trigger__results', loadingResults ? 'searchwp-cro--is-loading' : '']">

				<div
					v-if="loadingResults"
					class="searchwp-cro-trigger__results--loading-indicator">
					<spinner
						:animation-duration="1000"
						:dot-size="15"
						:dots-num="3"
						color="#444444"
					/>
				</div>

				<div v-else>

				</div>

			</div>

			<ol>
				<li v-for="entry in promoted" :key="'promoted' + entry.ID">
					{{ entry.title }} ({{ entry.ID }})
				</li>
			</ol>
		</div>

	</div>

</template>

<script>
import Vue from 'vue';

export default {
	name: 'Trigger',
	components: {},
	props: {
		query: {
			type: String,
			default: '',
			required: true
		},
		exact: {
			type: Boolean,
			default: true
		},
		engine: Object,
		promoted: {
			type: Array,
			default: [],
			required: true
		},
		expanded: {
			type: Boolean,
			default: false
		}
	},
	components: {},
	methods: {
		toggleDetails() {
			this.showingDetails = !this.showingDetails;
		},
		remove() {
			alert("need to emit removal");
		},
		getSearchResults() {
			const data = {
				_ajax_nonce: _SEARCHWP_CRO_VARS.nonce,
				action: 'get_search_results',
				query: this.query,
				engine: this.engine
			};

			return new Promise(function(resolve, reject) {
				jQuery.post(ajaxurl, data, function(response) {
					if (response.success) {
						resolve(response);
					} else {
						reject(response);
					}
				});
			});
		}
	},
	created() {
		this.showingDetails = this.expanded;
		this.triggerEngine = this.engine;
	},
	computed: {
		engineOptions() {
			let engines = [];

			for (var engine in this.$root.$data.engines) {
				if (this.$root.$data.engines.hasOwnProperty(engine)) {
					const thisEngine = this.$root.$data.engines[engine];
					engines.push({
						name: engine,
						label: thisEngine.hasOwnProperty('searchwp_engine_label') ? thisEngine.searchwp_engine_label : 'Default'
					});
				}
			}

			return engines;
		},
		modifiedResults() {
			// modifiedResults are the results that have NOT been promoted for this trigger.
			// TODO: Return the diff between results and promotedResults.
		}
	},
	data() {
		return {
			showingDetails: false,
			triggerEngine: {},
			loadingResults: true,
			results: [], // The full results set for this trigger.
			promotedResults: [], // The promoted results for this trigger.
		}
	}
}
</script>

